<template>
	<view>
		<view class="orderItem">
			<view class="orderInfo">
				<uni-row class="Ordertitle">
					<uni-col :span="2"><image class="titleIcon" src="../../static/img/orderBus.png"/></uni-col>
					<uni-col :span="6" class="titleText">企业订单</uni-col>
					<uni-col :offset="10" :span="6" class="orderState">
						<!-- 已完成 -->{{state}}</uni-col>
				</uni-row>
				<uni-row class="OrderBody">
					<uni-col  :span="4">
						<view class="orderImg">
							<image class="orderIcon" src="../../static/img/orderBus.png"/>
						</view>
					</uni-col>	
					<uni-col :span="20">
						<ul>
							<li>时间：{{orderItem.time}}</li>
							<li>地点：{{orderItem.start}}->{{orderItem.end}}</li>
							<!-- <li>司机电话：{{orderItem.phone}}</li> -->
						</ul>
					</uni-col>
				</uni-row>
				<uni-row class="cancel" >
					<uni-col :span="6" :offset="18" >
						
						<button 
							v-if="orderItem.state==0" 
							class="confirmOrder" 
							type="default" 
							size="mini" 
							@click="confirmOrder">待确认</button>
						<button
							v-if="orderItem.state==1"
							class="EDconfirmOrder" 
							type="default" 
							size="mini" 
							:disabled="true">已确认</button>
						<uni-popup ref="popup" type="center">
							<view class="confirmDetail"> 
								<uni-row class=“>
									<uni-col :span="2" :offset="21">
										<uni-icons @click="close" type="closeempty" size="30"></uni-icons>
									</uni-col>
								</uni-row>
								<uni-row class=“><uni-col :offset="1"><text class="itemTitle">您需要确认一下订单：</text></uni-col></uni-row>
								<uni-row class=“>
									<uni-col :offset="1"><text class="itemTitle">行程信息：</text></uni-col>
									<uni-col :offset="1">
										<ul>
											<li><text class="itemTitle">时间：</text><text>{{orderItem.time}}</text></li>
											<li><text class="itemTitle">地点：</text><text>{{orderItem.start}}->{{orderItem.end}}</text></li>
											<li v-if="orderItem.extraMoney"><text class="itemTitle">额外费用：</text><text>{{orderItem.extraMoney}}</text></li>
											<li v-if="orderItem.reason"><text class="itemTitle">原因：</text><text>{{orderItem.reason}}</text></li>
										</ul>
										<text><text class="itemTitle">金额:</text><text>￥3456</text></text>
									</uni-col>
								</uni-row>
								
								<uni-row class="orderRow">
									<uni-col><button type="primary" @click="ackOrder()" >确认订单</button></uni-col>
								</uni-row>
							</view>
						</uni-popup>
					</uni-col>
				</uni-row>
				<uni-row>
					<uni-col :span="6" :offset="18">
						<button
							v-if="orderItem.state==0" 
							class="cancelOrder" type="default" 
							size="mini" 
							@click="cancelOrder">取消订单</button>
					</uni-col>
				</uni-row>
				<uni-row v-if="orderItem.state==0">
					<uni-col :span="6" :offset="18">
						<button
							class="cancelOrder" type="default" 
							size="mini" 
							@click="turnMap">查看地图</button>
					</uni-col>
				</uni-row>
			</view>
			<!-- <view class="orderButton">
				<view class="buttonTurn"><button type="primary" @click="qrcodeTurn">二维码</button></view>
				<view class="buttonTurn"><button type="primary" @click="mapTurn">地图</button></view>
			</view> -->
		</view>
	</view>
</template>

<script>
	export default {
		name:'orderItem',
		props:{
			orderItem:{
				type:Object,
				default(){
					return {
						orderItem:{}
					}
				}
			}
		},
		data(){
			return{
				
			}
		},
		computed:{
			state:function(){
				if(this.orderItem==null)
				{
					return `error`
				}
				else
				{
					switch(this.orderItem.state){
						case 1: {
							return `已确认`;
							break;
						}
						case 2:{
							return `已取消`;
							break;
						}
						case 0:{
							return `正在进行`;
							break;
						}
					}
				}
			}
		},
		methods:{
				close(){
					this.$refs.popup.close()
				},
				cancelOrder(){
					uni.showModal({
						content:'确定取消订单？'
					})
				},
				ackOrder(){
					var that=this;
					console.log(that.orderItem.id)
					uni.request({
						url:'http://mrbus.net/api/eorder/ack',
						method:'POST',
						data:that.orderItem.id,
						header:{
							"content-type":"application/x-www-form-urlencoded"
						},
						success: (res) => {
							console.log(res.data)
							if(res.data.status_code==200){
								uni.showToast({
									title:'确认成功！'
								})
								setTimeout(function(){
									uni.reLaunch({
										url:'../history/enterpriseOrder'
									})
								},1000)
							}
						}
					})
				},
				confirmOrder(){
					this.$refs.popup.open()
				},
				turnMap(){
					var that=this;
					uni.navigateTo({
						url:'../map/EnterpriseMap?orderId='+that.orderItem.id
					})
				}
		},
		created() {
			// console.log(this.orderItem.id)
		}
	}
</script>

<style>
	.orderItem{
		position: relative;
		width: 100%;
		border: 2px solid #ffffff; 
		padding: 3px;
		border-radius: 10px;
		background-color: #FFFFFF;
	}
	.orderInfo{
		
	}
	.Ordertitle{
		padding: 2px;
		border-bottom: 1px solid #f0f0f0;
	}
	.titleText{
		font-size: 14px;
		line-height:31.25px;
		font-weight: 600;
	}
	.orderState{
		font-size: 14px;
		line-height:31.25px;
		color: #4c4c4c;
	}
	.orderImg{
		width: 100%;
		height: 100%;
		margin: 0 auto;
	}
	ul{
		margin: 0;
		padding-left: 20px;
	}
	ul li{
		list-style: none;
		padding: 2.5px;
		font-size: 12px;
	}
	.OrderBody{
		margin-top: 4px;
	}
	.orderButton{
		display: flex;
		
	}
	.buttonTurn{
		flex: 1;
		padding: 10px;
	}
	.buttonTurn button{
		font-size: 16px;
		font-weight: 300;
	}
	.orderIcon{
		width: 15vw;
		height: 15vw;
	}
	.titleIcon{
		width: 7vw;
		height: 7vw;
	}
	.cancel{
		margin-top: 2px;
	}
	.cancelOrder{
		font-size: 12px;
		font-weight: 500;
		color: #FFFFFF;
		background-color: #f50000;
		border-radius: 5px;
	}
	.confirmOrder{
		font-size: 12px;
		font-weight: 500;
		color: #FFFFFF;
		background-color: #ffaf0e;
		border-radius: 5px;
	}
	.EDconfirmOrder{
		font-size: 12px;
		font-weight: 500;
		color: #676767;
		background-color: #e3e7d9;
		border-radius: 5px;
	}
	.confirmDetail{
		background-color: #FFFFFF;
		border: 1px solid #F0F0F0;
		border-radius: 5px;
		padding: 5px;
		margin: 5px;
	}
	.confirmDetail text{
		font-size: 14px;
	}
	.orderRow{
		margin-top: 5px;
	}
	.itemTitle{
		font-weight: 500;
	}
</style>
